* {
  box-sizing: border-box;
}

/* Layout Classes */
.fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: hidden auto;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: black;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.contentarea {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.topnavbar {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  height: 5.5rem;
  width: 100%;
  color: #fa4454;
  background-color: black;
}

.topnavtab {
  margin: 0 auto;
  font-size: 28px;
}

.topnavtab:nth-of-type(1n) {
  margin-left: 0;
}

.row {
  display: flex;
}

.item {
  margin: 2rem auto;
}

/* Text Classes */
.logo {
  display: flex;
  margin-left: 2rem;
  font-size: 60px;
  color: #fa4454;
}

.dividerline {
  width: 10%;
  height: 100%;
  overflow: hidden;
  justify-content: center;
  font-size: 35px;
  display: flex;
  color: #505050;
}

.dividerline1 {
  height: 14rem;
  width: 1rem;
  margin-right: 1rem;
  transform: translate(0px, -4rem) rotate(15deg);
  z-index: -1;
  background-color: #1a1a1a;
}

.dividerline2 {
  height: 14rem;
  width: 1rem;
  transform: translate(0px, -4rem) rotate(15deg);
  z-index: -1;
  background-color: #1a1a1a;
}

.tab {
  color: whitesmoke;
  text-decoration: none;
}

.tab:hover {
  color: #fa4454;
}

.desktopnav {
  display: flex;
  width: 80%;
  margin-right: 2rem;
}

.mobilenav {
  display: none;
}

button {
  padding: 0;
}

/* Dropdown Button */
.dropbtn {
  color: whitesmoke;
  font-size: 28px;
  border: none;
  display: flex;
  background-color: transparent;
}

.dropbtn:hover {
  color: #fa4454;
}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display: inline-block;
  float: right;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: black;
  min-width: 160px;
  right: 0;
  z-index: 1;
}

.role-dropdown-content {
  display: none;
  position: absolute;
  min-width: 4rem;
  background-color: black;
  right: 0;
  z-index: 1;
}

/* Links inside the dropdown */
.dropdown-content a {
  color: #fa4454;
  font-size: 18px;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.role-dropdown-content img {
  display: block;
  margin-top: 0.5rem;
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {
  background-color: #343434;
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block;
  left: initial;
}

.dropdown:hover .role-dropdown-content {
  display: block;
  left: initial;
}

.hamburgermenucontainer {
  display: none;
}

/* General Styling */
body {
  margin: 0;
  background-color: black;
  font-family: Helvetica, sans-serif;
  color: whitesmoke;
  height: 100%;
  width: 100%;
}

@media (max-width: 1024px) {
  .desktopnav {
    display: none;
  }

  .mobilenav {
    display: flex;
    width: 33%;
    margin: 0 auto;
  }

  .mobilenav .topnavtab:nth-of-type(1n) {
    margin: 0 auto;
  }

  .dropbtn {
    font-size: 42px;
  }

  .logo {
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: center;
    width: 33%;
    font-size: 60px;
    color: #fa4454;
  }

  .dividerline {
    margin-left: auto;
    margin-right: auto;
    width: 33%;
    font-size: 35px;
    color: #343434;
  }

  .hamburgermenucontainer {
    width: 3rem;
    display: flex;
    flex-direction: column;
  }

  .hamburgerlinecontainer {
    display: flex;
    height: 0.5rem;
    width: 3rem;
    overflow: hidden;
  }

  .hamburgerlinecontainer:nth-of-type(2n) {
    display: flex;
    height: 0.5rem;
    width: 3rem;
    margin: 0.5rem 0;
    overflow: hidden;
  }

  .hamburgerline1 {
    background-color: whitesmoke;
    height: 0.5rem;
    width: 1rem;
  }

  .hamburgerline2 {
    background-color: whitesmoke;
    height: 0.5rem;
    width: 1.75rem;
  }

  .hamburgerlineender {
    background-color: whitesmoke;
    width: 0.5rem;
    height: 2rem;
    margin-left: auto;
    transform: translate(0px, -1px) rotate(15deg);
  }

  .hamburgerline3 {
    background-color: whitesmoke;
    height: 0.5rem;
    width: 2.5rem;
  }
}

@media (max-width: 425px) {
  .logo {
    margin-left: auto;
    margin-right: auto;
    width: 33.4%;
    font-size: 60px;
    color: #fa4454;
  }

  .dividerline {
    width: 33.3%;
    justify-content: center;
    font-size: 35px;
    display: flex;
    color: #505050;
  }

  .mobilenav {
    display: flex;
    width: 33.3%;
    margin: 0 auto;
  }

  .pluspluscontainer {
    display: flex;
    font-size: 40px;
    line-height: 24px;
    flex-direction: column;
    margin-top: 9px;
  }
}
